@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Passions+Conflict&display=swap');

body {
  font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif;
  font-size: 1.05rem;
  color: #222;
  /* Frutiger Aero 2025: glassy, soft, modern gradient */
  background: linear-gradient(135deg, #f5f5dc 0%, #e8f5e9 40%, #e0f7fa 100%);
  background-attachment: fixed;
  letter-spacing: 0.01em;
}

.font-logo {
  font-family: 'Passions Conflict', cursive;
  font-size: 2.1em;
  font-weight: 400;
  letter-spacing: 0.04em;
  line-height: 1.1;
}

h1, h2, h3, h4, h5, h6 {
  color: var(--primary);
  font-family: 'Inter', 'Helvetica Neue', Arial, sans-serif;
  font-weight: 700;
  letter-spacing: -0.01em;
  /* Frutiger Aero style: clean, bold, modern */
}

.text-primary {
  color: var(--primary) !important;
}

.text-accent {
  color: var(--accent) !important;
}

.bg-primary {
  background-color: var(--primary) !important;
  color: var(--primary-foreground) !important;
}

.bg-accent {
  background-color: var(--accent) !important;
  color: var(--accent-foreground) !important;
}

.bg-secondary {
  background-color: var(--secondary) !important;
  color: var(--secondary-foreground) !important;
}

.text-muted-foreground {
  color: var(--muted-foreground) !important;
}

/* Add a subtle shadow for Frutiger Aero depth */
.hero-shadow {
  text-shadow: 0 2px 8px rgba(112, 130, 56, 0.08);
}

input, button, textarea, select {
  font-family: inherit;
}

